<template>
    <div class="carousel-container">
        <div class="carousel-content">
            <transition name="fade" mode="out-in">
                <img 
                    v-if="images.length > 0"
                    :src="images[current]" 
                    :key="current" 
                    alt="Image Carousel" 
                    class="carousel-image"
                    @click="openModal(images[current])"
                />
            </transition>
            <button v-if="images.length > 1" @click="prev" class="carousel-btn prev">
                <svg viewBox="0 0 24 24" width="24" height="24" fill="currentColor"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"/></svg>
            </button>
            <button v-if="images.length > 1" @click="next" class="carousel-btn next">
                <svg viewBox="0 0 24 24" width="24" height="24" fill="currentColor"><path d="M8.59 16.59L10 18l6-6-6-6-1.41 1.41L13.17 12z"/></svg>
            </button>
        </div>
        <div v-if="images.length > 1" class="carousel-dots">
            <span
                v-for="(_, index) in images"
                :key="index"
                class="dot"
                :class="{ active: current === index }"
                @click="goTo(index)"
            ></span>
        </div>

        <!-- Modale pour le zoom -->
        <div v-if="isModalVisible" class="modal-overlay" @click="closeModal">
            <span class="modal-close" @click.stop="closeModal">&times;</span>
            <img :src="zoomedImage" alt="Image zoome" class="modal-content" @click.stop>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        images: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            current: 0,
            isModalVisible: false,
            zoomedImage: ''
        }
    },
    methods: {
        next() {
            if (this.images.length === 0) return;
            this.current = (this.current + 1) % this.images.length;
        },
        prev() {
            if (this.images.length === 0) return;
            this.current = (this.current - 1 + this.images.length) % this.images.length;
        },
        goTo(index) {
            this.current = index;
        },
        openModal(imageSrc) {
            if (!imageSrc) return;
            this.zoomedImage = imageSrc;
            this.isModalVisible = true;
        },
        closeModal() {
            this.isModalVisible = false;
            this.zoomedImage = '';
        }
    }
}
</script>

