<template>
    <div id="projects" class="projects">
        <div class="navigator">
            <div class="actions">
                <div class="close"></div>
                <div class="minus"></div>
                <div class="zoom"></div>
            </div>

            <div class="content">
                <div class="left">

                    <div class="search divider">
                        <input type="text" v-model="form.search" placeholder="Search for a project..." />
                    </div>

                    <ul>
                        <li v-for="project of projects"
                            :key="project.title"
                            :class="[{ active: activeProject === project, notified: notifications[project.key] }]"
                            @click="activeProject = project" v-show="!project.hide">
                            <Avatar :avatar="project.avatar" :color="project.avatarColor" />
                            <span>{{ project.title }}</span>
                            <span class="notifications" v-if="notifications[project.key]"><font-awesome-icon
                                    icon="bell" /></span>
                        </li>
                    </ul>
                </div>

                <div class="conversation" v-if="activeProject">
                    <div class="title">
                        <Avatar :avatar="activeProject.avatar" :color="activeProject.avatarColor" />
                        <span>{{ activeProject.title }} </span>
                        <span v-if="activeProject.logos && activeProject.logos.length">&nbsp;|&nbsp;</span>
                        <img v-for="(logo, index) in activeProject.logos" :key="index" height="20"
                            :src="require('../assets/img/logos/' + logo + '.png')" :alt="logo" :title="logo">
                    </div>

                    <div class="messages" ref="conversation">

                        <div class="message-wrapper" v-for="(message, index) of activeProject.messages" :key="index">
                            <template v-if="message.me">
                                <div class="message">
                                    <template>
                                        <Avatar :color="'rgb(37, 128, 205)'" />
                                        <span> Giovanni </span>
                                        <span class="createdAt"> {{ formatDate(new Date(message.createdAt)) }} </span>
                                    </template>
                                </div>

                                <div class="message-content" v-html="message.content"></div>
                            </template>

                            <template v-else>
                                <div class="message">
                                    <template>
                                        <Avatar :avatar="message.User.avatar"
                                            :color="message.User.avatarColor" />
                                        <form @submit.prevent="sendNickname"
                                            v-if="message.User.id === userId && form.nickname.edit">
                                            <input type="text" placeholder="Pseudo" v-model="form.nickname.value"
                                                autofocus>
                                        </form>
                                        <span v-else>{{ password ? message.User.nickname || message.User.id :
                                            message.User.nickname || 'Vous' }}</span>
                                        <span class="createdAt"> {{ formatDate(new Date(message.createdAt)) }} </span>
                                    </template>
                                </div>

                                <div class="message-content" v-html="message.content"></div>
                            </template>
                        </div>

                        <div class="message-wrapper">
                            <div class="message">
                                <Avatar :avatar="activeProject.avatar" :color="activeProject.avatarColor" />
                                <span>{{ activeProject.title }}</span>
                            </div>

                            <div class="carousel" v-if="activeProject.carousel">
                                <Carousel :images="activeProject.carousel.map(name => `/img/${activeProject.key}/${name}`)" />
                            </div>

                            <div class="message-content" v-html="projectDescription"></div>
                        </div>

                    </div>

                    <form class="input" @submit.prevent="send">
                        <input :type="activeProject.key === 'giovanni' && conversation.step === 'awaiting_email' ? 'email' : 'text'" v-model="form.message" :placeholder="activeProject.key === 'giovanni' && conversation.step === 'awaiting_email' ? 'Votre e-mail pour la rponse...' : $t('question')" />
                        <button type="submit" :disabled="!form.message">
                            <font-awesome-icon icon="paper-plane" />
                        </button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Avatar from './Avatar.vue';
import moment from 'moment';
import { projects, user } from '../data.js';
import { marked } from "marked";
import axios from 'axios';
import Carousel from './Carousel.vue';

export default {
    beforeMount() {
        this.projects = projects;
        this.user = user;
        // Hardcoded "Giovanni" project for contact
        this.projects.unshift({
            key: 'giovanni',
            title: 'Giovanni',
            avatar: 'moi.webp',
            messages: [],
        });
        this.activeProject = this.projects[0];

        // Check for saved email in localStorage
        const savedEmail = localStorage.getItem('user-email');
        if (savedEmail) {
            this.conversation.email = savedEmail;
        }
    },
    components: {
        Avatar,
        Carousel,
    },
    data() {
        return {
            form: {
                search: undefined,
                message: undefined,
                nickname: {
                    edit: false,
                    value: undefined,
                },
            },
            conversation: {
                email: '',
                question: '',
                step: 'start', // 'start', 'awaiting_email', 'done'
            },
            user: undefined,
            notifications: {},
            activeProject: undefined,
            projects: [],
            projectDescription: '',
        };
    },
    methods: {
        formatDate(date) {
            return moment(date).format('L') + ' ' + moment(date).format('LT');;
        },
        scrollToBottom() {
            this.$nextTick(() => {
                if (this.$refs.conversation) {
                    this.$refs.conversation.scrollTop = 0;
                }
            });
        },
        send() {
            if (!this.form.message) return;

            const userMessageContent = this.form.message;

            // Push user's message to the chat
            this.activeProject.messages.unshift({
                content: userMessageContent,
                createdAt: new Date(),
                User: { nickname: "Vous", avatarColor: 'rgb(37, 128, 205)' },
            });
            this.form.message = '';

            if (this.conversation.step === 'start') {
                this.conversation.question = userMessageContent;

                // If email is already known
                if (this.conversation.email) {
                    setTimeout(() => {
                        axios.post('https://contact.scarpellino.fr/send-email', {
                            projectTitle: this.activeProject.title,
                            message: this.conversation.question,
                            email: this.conversation.email
                        }).then(response => {
                            this.activeProject.messages.unshift({
                                content: "Message bien reu ! Je vous rpondrai sur l'e-mail que vous m'avez dj fourni. ",
                                createdAt: new Date(),
                                me: false,
                                User: { title: this.activeProject.title, avatar: this.activeProject.avatar, nickname: this.activeProject.title }
                            });
                        }).catch(error => {
                            this.activeProject.messages.unshift({
                                content: "Oups, il y a eu un problme en envoyant votre message. Veuillez ressayer plus tard.",
                                createdAt: new Date(),
                                me: false,
                                User: { title: this.activeProject.title, avatar: this.activeProject.avatar, nickname: this.activeProject.title }
                            });
                        });

                        this.conversation.step = 'done';
                        this.scrollToBottom();
                    }, 500);
                } else {
                    // If email is not known, ask for it
                    setTimeout(() => {
                        this.activeProject.messages.unshift({
                            content: "Parfait ! Pour que je puisse vous rpondre, laissez-moi simplement votre adresse e-mail.",
                            createdAt: new Date(),
                            me: false,
                            User: { title: this.activeProject.title, avatar: this.activeProject.avatar, nickname: this.activeProject.title }
                        });
                        this.conversation.step = 'awaiting_email';
                        this.scrollToBottom();
                    }, 500);
                }
            } else if (this.conversation.step === 'awaiting_email') {
                const userEmail = userMessageContent;
                this.conversation.email = userEmail;
                localStorage.setItem('user-email', userEmail);

                setTimeout(() => {
                    axios.post('https://contact.scarpellino.fr/send-email', {
                            projectTitle: this.activeProject.title,
                            message: this.conversation.question,
                            email: this.conversation.email
                    }).then(response => {
                        this.activeProject.messages.unshift({
                            content: "Message bien reu ! Je vous rpondrai sur l'e-mail que vous m'avez dj fourni. ",
                            createdAt: new Date(),
                            me: false,
                            User: { title: this.activeProject.title, avatar: this.activeProject.avatar, nickname: this.activeProject.title }
                        });
                    }).catch(error => {
                        this.activeProject.messages.unshift({
                            content: "Oups, il y a eu un problme en envoyant votre message. Veuillez ressayer plus tard.",
                            createdAt: new Date(),
                            me: false,
                            User: { title: this.activeProject.title, avatar: this.activeProject.avatar, nickname: this.activeProject.title }
                        });
                    });

                    this.conversation.step = 'done';
                    this.scrollToBottom();
                }, 500);
            }
            this.scrollToBottom();
        },
        editNickname(nickname) {
            this.form.nickname.value = nickname || 'Vous';
            this.form.nickname.edit = true
        },
        sendNickname() {
            this.user.nickname = this.form.nickname.value;
            this.form.nickname.edit = false;
        }
    },
    watch: {
        'form.search'(search) {
            if (!search) {
                for (let project of this.projects)
                    this.$set(project, 'hide', false);
            } else {
                for (let project of this.projects)
                    this.$set(project, 'hide', !project.title.toLowerCase().includes(search.toLowerCase()));
            }
        },
        activeProject: {
            async handler(project) {
                if (!project) return;
                this.$set(this.notifications, project.key, false);

                // Reset conversation step for the new chat thread, but keep the email
                this.conversation.step = 'start';
                this.conversation.question = '';

                if (project && project.markdown) {
                    try {
                        const markdownModule = await import(`@/assets/markdown/${project.key}.md`);
                        this.projectDescription = marked(markdownModule.default);
                    } catch (e) {
                        console.error(e);
                        this.projectDescription = 'Error loading markdown.';
                    }
                } else if (project) {
                    this.projectDescription = this.$t('projects.' + project.key);
                } else {
                    this.projectDescription = '';
                }

                this.scrollToBottom();
            },
            immediate: true
        },
    },
};
</script>

